// Streams and Timeline
// --------------------------------------------------
.stream-list {
  position: relative;
}

.stream-group{
  margin-bottom: 10px;
  &:last-child{margin-bottom: 0px; }
}

.stream-title {
  padding: @grid-gutter-width*((1/3)) @grid-gutter-width*0.5 @grid-gutter-width*((1/3)*0.9);
  background: white;
  border-bottom: 2px solid lighten(@clouds, 3%);
  font-size: @font-size-small*0.92;
  //color: @text-color;
  font-weight: 700;
  text-transform: uppercase;
  position: relative;
  z-index: 1;
  cursor: pointer;
  width: auto; min-width: 85px; text-align: center;
  display: inline-block;
  a {
    color: @link-color;
    text-transform: none;
    font-weight: normal;
    text-decoration: none;
  }
}

.stream-line, .stream-bridge {
  position: absolute;
  background: lighten(@clouds, 3%);
  z-index: -1px;
  display: block;
}
.stream-line{
  width: 5px;
  left: 38px;
  top: 15px;
  bottom: 0px;
  cursor: pointer;
}
.stream-bridge{width: 90px; height: 2px; top: 40px; left: -45px;}

.stream-input {
  margin-bottom: @grid-gutter-width*0.5
}

.stream-item {
  //min-height: 140px;
}

.stream {
  margin: 0;
  padding: 0;
  list-style: none;
  li {
    position: relative;
    .more {
      padding: 10px;
      margin: 5px 0 0 0px;
      border-radius: 1px;
      background-color: #D9EDF7;
      color: #3A87AD;
      cursor: pointer;
      text-align: center;
      position: relative;
      z-index: 2;
      display: block;
    }
    .input {
      .form-control {
        .box-sizing(border-box);
      }
    }
    .link, .item {
      text-decoration: none;
      padding: @grid-gutter-width*0.5;
      display: block;
      font-size: @font-size-base;
      min-height: 65px;
      &.unread {
        background: #d9edf7
      }
      &.open {
        background: fade(@peter-river, 10%);
      }
      &.has-featured-img {
        padding: @grid-gutter-width;
        padding-left: 60px+ @grid-gutter-width*(5/3);
        min-height: (60px+@grid-gutter-width);
      }
      //&:hover{ background: #f9f9f9 }
      .clearfix();
      &.reading, &.writing {
        background: white;
        margin-bottom: @grid-gutter-width *0.5;
        .title {
          margin-bottom: 10px
        }
      }
      //&.reading{border: 1px solid transparent}
    }
    .media {
      .clearfix();
      display: inherit;
      img, figure {
        max-width: 100%;
        .clearfix();
      }
      .clearfix();
      .media-grid {
        margin: 15px 15px 0;
      }
    }
    .actions {
      //margin-top: 10px;
    }

    .featured-img, .featured-media, .cover-img, .extra, .extra-plus {
      max-height: 50px;
      max-width: 50px;
      position: absolute;
      left: @grid-gutter-width;
      // top: 0;
      z-index: 2;
      //img{ .border-radius(50%); }
      label, .label, .badge {
        margin: 0px;
      }
      &.btn {
        text-align: center;
        vertical-align: middle;
        font-weight: 700;
      }
    }
    .actions {
      display: block;
      .nav {
        &.pull-left {
          margin-left: -5px;
        }
        li {
          display: inline-block;
          > a {
            padding: 5px;
            line-height: 1;
            font-size: 90%;
            display: inline-block;
          }
        }
      }
    }
    .meta {
      margin: @grid-gutter-width*0.5 0
    }
    .extra, .extra-plus {
      width: 60px;
     // top: @grid-gutter-width;
      align-content: center;
      text-align: center;
      text-transform: uppercase;
      font-size: 8px;
      line-height: 12px;
      top: @grid-gutter-width;
      &~ .extra, &~ .extra-plus {
        top: 0px+@grid-gutter-width * (8/3);
      }
      .checkbox,
      .radio {
        margin-left: 1px;
      }
    }

    .featured-img, .featured-media, .cover-img {
      &~ .extra, &~ .extra-plus {
        top: 50px+@grid-gutter-width * (4/3);
        &~ .extra, &~ .extra-plus {
          top: 50px+@grid-gutter-width * (8/3);
        }
      }
    }
    .select {
      position: absolute;
      top: @grid-gutter-width*1.1;
      left: @grid-gutter-width;
      & ~ .link {
        display: block;
        padding-left: @grid-gutter-width*2.5
      }
      &:checked {
        & ~ .link {
          background: #fcf8e3
        }
      }
    }
    .title, .meta {
      color: @gray-light;
      .author {
        color: @gray-dark;
        font-weight: 700;
        margin-bottom: 10px;
      }
      .time {
        margin: 0 5px;
      }
    }
    .subject {
      margin-bottom: 10px;
      font-size: 15px;
	  position: relative;
      //display: inline-block;
      &.heading {
        //line-height: 50px;
        font-weight: 400 ;
      }
	  .pins{ position: absolute; right: 0; }
      .subject, .target{margin-right: 5px;  a{ font-weight: normal !important } }
      .action{ color: @silver;  font-weight: normal !important}
      .time{ font-weight: 400; font-size: @font-size-base;  color: @silver; margin-left: 20px; }
      .privacy{margin-left: 20px; color: @silver }
    }
    .content {
      clear: both;
      color: @text-color;
    }
    .participants{
        position: relative;
        margin-bottom: 10px;
        .opacity(0.5);
        .person{
          margin: 0 -30px 1px 0 ;
          max-width: 50px;
          max-height: 50px;
          cursor: pointer;
          display: none;
          background: white;
		  .person-status{ display: none; }
          &:last-child {
            margin-right: 0px;
          }
          &:nth-child(-n+3) {
            display: inline-block
          }
        }
      &:hover {
        .opacity(1);
        background: #FFF;
        .person {
          position: relative;
          margin: 0 -5px 1px 0 ;
          display: inline-block;
          .person-status{ display: block; }
          &:last-child {
            margin-right: 0px
          }
        }
      }
    }
    &.roll {
      .featured-img, .featured-media, .cover-img {
        top: 15px;
      }
    }
    &.bottom-border {
      border-bottom: 1px solid #f7f7f7;
    }
    &:last-child {
      border-bottom: none
    }
    > .stream-list {
      margin-left: 60px+@grid-gutter-width * (5/3);
      position: relative;
      //border-top: 1px solid #f7f7f7;
      > .stream-line {
        left: 39px
      }
      ul {
        li {
          .item, .link {
            &.reading, &.writing {
              background: #fcfcfd;
            }
          }
          .content {
            color: @concrete;
          }
          .featured-img, .featured-media, .cover-img {
            & ~ .extra, & ~ .extra-plus {
              top: 50px+@grid-gutter-width * (4/3);
              & ~ .extra, & ~ .extra-plus { //max of up to two tags;
                top: 50px+@grid-gutter-width * (8/3);
              }
            }
          }

        }
      }
    }
    &.open {
      .holder {
        background: #fcfcfd;
        border: 1px solid #f7f7f7;
        margin-bottom: -1px;
      }
    ;
      .title {
        margin-bottom: 10px;
      }
      .content.reading {
        color: @gray-dark;
      }
    }
    .stream-content {
      position: relative;
      .item;
      padding: 0px;
      .featured-img, .featured-media, .cover-img, .extra {
        left: -(60px+ @grid-gutter-width*0.5);
        top: 0px;
      }
    }
    .stream-media {
      margin: 15px 0;
      & ~ .stream-item {
        padding-top: 15px;
      }
    }
    .stream-subject {
      margin: 0px -15px 0px -90px;
      padding: 0 15px 15px;
      font-weight: 600;
      border-bottom: 1px solid #EEE;
    }
  }
  .nav-pills{
    li > a{
      padding: 0px 10px; color: @link-color;
    }
    li:first-child >a{ padding-left: 0}
    li:last-child > a{padding-right: 0}
    li:hover, li.active{
      &>a{ background: transparent; }
    }
  }
}

.stream {
  &-grid {
    position: relative;
    height: 100%;
    .stream-line {
      display: none;
    }
    .stream {
      //.img-responsive{width: 100%; .box-sizing(border-box)}
      .stream-item {
        width: 418px;
        .featured-img, .featured-media, .cover-img, .extra {
          //top:@grid-gutter-width*(1/3);
          & ~ .extra {
            top: 75px;
          }
        }
      }
    }
  }
  &-list {
    &.form-list {
      border-right: 1px solid transparent
    }
  }
}

.chat-board {
  .panel-input {
    border: none;
  }
}

.chat-thread {
  position: absolute;
  bottom: 167px;
  left: 0;
  right: 0;
  top: 0;
  overflow-y: auto;
  overflow-x: hidden;
  border-left: 1px solid transparent;
  padding: @grid-gutter-width * 0.5;
}

.chat-input {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  border-left: 1px solid transparent;
  .panel-input {
    margin: 0;
    border-left: none;
    border-right: none;
  }
  &:not(:hover) ~ .chat-thread {
    bottom: 167px
  }
  &:hover ~ .chat-thread {
    bottom: 167px
  }
}

.chat-message {
  padding: 0px 10px 10px;
  .message {
    img.author-pic {
      position: absolute;
      left: 10px;
      top: 10px;
    }
    .content {
      width: auto;
      background: fade(@green-sea, 10%);
      margin: 0 0 0 50px;
      padding: 10px;
      float: right;
    }
    img.author-pic, .content {
      display: inline-block;
    }
    img.author-pic ~ .content {
      background: lighten(@silver, 20%);
      float: left;
    }
    .clearfix();
  }
  &:last-child {
    padding-bottom: 10px
  }
}



//Specialised Streams
.task-list{
  li {
    .link,.link.has-featured-img{
       //min-height: 40px;
      border-bottom: 1px dashed lighten(@clouds, 3%);
    }
    &:last-child{
      .link,.link.has-featured-img{ border-bottom: 0px solid transparent; }
    }
    &.task-time{}
    &.task-done{
      .link,.link.has-featured-img{
        h3{ text-decoration: line-through; .opacity(0.5) }
        .content{text-decoration: line-through; .opacity(0.5) }
      }
    }
  }
}

.stream-list{
	&.collapsed{	
		.stream-line, .stream-title{ display: none; }
		.stream{
	li {
		border-bottom: 1px solid #f7f7f7;
		.person{
			img.person-photo{ width: 32px; height: 32px}
		}
		.subject{ 
			line-height: 30px; margin-bottom:0px;
			.pins{ top: 10px; }
		}
	    .body,.actions,.extra, .extra-plus{
			display: none;
		}
	    &:last-child {
	      border-bottom: none
	    }
		.item,.link{ padding-left: 70px !important; min-height: 32px!important}
	}
}
	}
}
